import React,{Component} from "react";
import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  Breadcrumb,BreadcrumbItem,Button,Row,Label,Col,Modal,ModalBody,ModalHeader,
} from "reactstrap";
import {Link } from 'react-router-dom';
import {LocalForm, Errors, Control} from 'react-redux-form';
import {Loading} from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

 

const required =(val) =>val&&val.length;
const maxLength = (len)=>(val)=>!(val)||(val.length<=len)
const minLength = (len)=>(val)=>(val)&&(val.length>=len)



class CommentForm extends Component {
    constructor(props)
    {
        super(props);
        this.state={
            modalOpen:false,
            disabled: true,
        }
        this.toggleModel=this.toggleModel.bind(this);
        this.submitHandle=this.submitHandle.bind(this);
    }
    onChange = (e) => {
      const comment = e.target.value.length;
      if (comment >= 3) {
        this.setState(() => ({ disabled: false }))
      } else if (!this.state.disabled) {
        this.setState(() => ({ disabled: true }))
      }
    }
    toggleModel=()=>
    {
        this.setState(
            {
                modalOpen:!this.state.modalOpen
            }
        )
    }

    submitHandle=(values)=>
    {
        this.toggleModel();
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
    }
    render() { 
      const label = this.state.disabled ? 'Disabled' : 'Submit';
        return ( 
          <React.Fragment>
            <Button className="bg-white text-dark" onClick={this.toggleModel}><i className="fa fa-pencil fa-lg"></i>{' '}Submit Comment</Button>
            <Modal isOpen={this.state.modalOpen} toggle={this.toggleModel}>
              <ModalHeader toggle={this.toggleModel}>Submit Comment</ModalHeader>
              <ModalBody>
                <LocalForm onSubmit={(values)=>this.submitHandle(values)}>
                  <Row className="form-group" >
                    <Label htmlFor="rating" md={4}>Rating</Label>
                    <Col md={12}>
                      <Control.select model=".rating" id="rating" name="rating" placeholder="username" className="form-control">
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                      </Control.select>
                    </Col> 
                  </Row>
                  <Row className="form-group">
                    <Label htmlFor="author" md={4}>Your Name</Label>
                    <Col md={12}>
                      <Control.text model=".author" id="author" name="author" placeholder="Your Name"
                      className="form-control"
                      validators={
                        { required, 
                          minLength:minLength(4),
                          maxLength:maxLength(15)
                        }
                      }
                      />
                       <Errors   className="text-danger font-italic"
                          model=".author"
                          show="touched"
                          messages={{
                            minLength: "Must be greater than 2 characters",
                            maxLength: "Must be 15 characters or less"
                          }}
                        >
                        </Errors>
                    </Col>
                   
                  </Row>
                  <Row className="form-group">
                    <Label htmlFor="comment" md={4}>Comment</Label>
                    <Col md={12}>
                      <Control.textarea model=".comment" id="comment" name="comment" 
                      className="form-control"
                      validators={{
                        required
                       }}
                       rows="6"
                       onChange={this.onChange}/>
                      <Errors   className="text-danger font-italic"
                          model=".comment"
                          show="touched"
                          messages={{
                            required:'Comment Required'
                         }}
                      >
                      </Errors>
                    </Col>
                  </Row>
                  <Row className="form-group">
                    <Col>
                      <Button style={Object.assign({}, styles.button, !this.state.disabled && 
                        styles.buttonEnabled)} disabled={this.state.disabled} >
                        {label}
                      </Button>
                    </Col>
                  </Row>
                </LocalForm>
              </ModalBody>
            </Modal>
          </React.Fragment>     
      );
    }
}

function RenderDish({dish}) {
   
      return (
        <div className="col-12 col-md-5 m-1">
            <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
            <Card>
              <CardImg top src={ baseUrl + dish.image} alt={dish.name} />
              <CardBody>
                <CardTitle>{dish.name}</CardTitle>
                <CardText>{dish.description}</CardText>
              </CardBody>
            </Card>
            </FadeTransform>          
        </div>
      );
    
}
function RenderComments({comments, postComment, dishId}) {
  const dishcomment = comments.map((comment) => {
    return (
      <Fade in>
      <ul className="list-unstyled" key={comment.id}>
          <p>{comment.comment}</p>
          <p>
            -- {comment.author}, &nbsp;
            {new Intl.DateTimeFormat("en-US", {
              year: "numeric",
              month: "long",
              day: "2-digit",
            }).format(new Date(comment.date))}
          </p>
        </ul>
      </Fade>
      );
    });
    return (
      <div className="col-12 col-md-5 m-1">
        <h4>Comments</h4>
        <div>
          <Stagger in>
            {dishcomment}
          </Stagger>
        </div>
        <CommentForm  dishId={dishId} postComment={postComment} />
      </div>
    );
  }
  const DishDetail = (props) => {
    if(props.isLoading){
      return(
        <div className="container">
          <div className="row">
            <Loading/>
          </div>
        </div>
      )
    }
    else if(props.errMess){
      return(
        <div className="container">
          <div className="row">
            <h4>{props.errMess}</h4>
          </div>
        </div>
      )
    }
    else if (props.dish != null)
      return (
        <div className="container">
           <div className="row">
            <Breadcrumb>
              <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
              <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
            </Breadcrumb>
            <div className='col-12'>
              <h3>{props.dish.name}</h3>
              <hr/>
            </div>
          </div>
          <div className="row">
            <RenderDish dish = {props.dish}/>
            <RenderComments comments = {props.comments}
              postComment={props.postComment}
              dishId={props.dish.id}
            />
          </div>
        </div>
      );
    else
      return(
        <div></div>
      );
   
  }

  const styles = {
    input: {
      width: 200,
      outline: 'none',
      fontSize: 20,
      padding: 10,
      border: 'none',
      backgroundColor: '#ddd',
      marginTop: 10,
    },
    button: {
      width: 180,
      height: 50,
      border: 'none',
      borderRadius: 4,
      fontSize: 20,
      cursor: 'pointer',
      transition: '.25s all',
    },
    buttonEnabled: {
      backgroundColor: '#07a4ff',
      width: 220,
    }
  }
export default DishDetail;
